.telegram-link {
  display: flex;
  align-items: center;
}


.telegram-link::before {
  content: '';
  display: block;
  width: 48px;
  height: 48px;
  background-image: url('./assert/Telegram.svg');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  margin: 5px;
}